/* simple flow */ 
.simple-flow-container{
  height: 100vh;
  width: 100%;
  z-index: 0;
  position: absolute;
}

/* mobile node */

.mobil {
  width: 120px;
  height: 70px;
  background-image: url("../../../images/nodes/mobil.svg");
  background-repeat: no-repeat;
  border: none;
  background-color: transparent;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white; /* Sets the text color to white */
  -webkit-text-stroke: 1px black; /* Adds a black stroke as a border */
  font-weight: bold; 
  font-size: 18px;
}   



.mobil .react-flow__handle-bottom, .mobil .react-flow__handle-top{
  visibility: hidden;
}

.mobil .react-flow__handle-top{
  top: 35px;
}

.mobil .react-flow__handle-bottom{
  bottom: 35px;
}

