.dialog-window-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #f5f5f5;
    display: inline-block;
    width: auto;
    height: auto;
    border: 2px solid #000;
    box-shadow: 0 0 24px rgba(0, 0, 0, 0.2);
    padding: 20px;
}

/* The Close Button */
.close {
    color: #aaaaaa;
    font-size: 28px;
    font-weight: bold;
    position: absolute;
    top: 3px;
    right: 15px;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
   
  /* summary window */
  
  .summary-prerequisities {
    display: flex;
   
  }
  
  .summary-content {
    margin-top: 1.3vh;
    margin-left: 3%;
    margin-right: 3%;
  }
  
  .column {
    margin: 0 10px;
    width: 50%;
    padding: 2%;
    border-radius: 0.5em;
    background: #e8e8e8;
    border: 1px solid #e8e8e8;
    box-shadow: 6px 6px 12px #c5c5c5,
                -6px -6px 12px #ffffff;
  }
  .summary-content p {
    font-size: 1.1rem;
    line-height: 1.5;
    margin-left: 1vw;
  }
  
  .summary-content ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .summary-content li {
    font-size: 1.1rem;
    padding: 0.4rem 0;
    position: relative;
  }
  
  .summary-content .column h2 {
    margin-top: 0;
    margin-bottom: 0.5em;
    font-size: 1.2rem;
  }
  
  .summary-content li:not(:last-child)::after {
    content: "";
    display: block;
    width: 100%;
    border-bottom: 1px solid #aaa9a9;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  
  .MuiBox-root{
    max-width: 80vw;
  }

  /* Next level modal window */

  .next-level-button {
    margin-top: 5vh;
    display: flex;
    justify-content: space-around;
  }
  